@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap);
:root {
    --text: #eeeeee;
    --background: #222831;
    --primary: #52C1FF;
    --nav-height: 70px;
}

*, body {
    margin: 0;
    box-sizing: border-box;
    color: #eeeeee;
    color: var(--text);
    font-family: 'Poppins', sans-serif;
    text-decoration: none;
}

.App {
    background: #222831;
    background: var(--background);
}

.workspace {
    width: 80%;
    margin: auto;
    z-index: 2;
}

button {
    font-size: 1.5rem;
    font-weight: 500;
    padding: 0.5rem 2rem;
    border: 2px solid #52C1FF;
    border: 2px solid var(--primary);
    box-sizing: border-box;
    border-radius: 500px;
    display: flex;
    align-items: center;
    justify-content: center;
    grid-gap: 1rem;
    gap: 1rem;
    cursor: pointer;
    background: #222831;
    background: var(--background);
}

button>img {
    width: 1.15rem;
    height: 1.15rem;
    object-fit: contain;
}

button[type="primary"] {
    border: none;
    background: #52C1FF;
    background: var(--primary);
}

input {
    background: #222831;
    background: var(--background);
    border: 2px solid #52C1FF;
    border: 2px solid var(--primary);
    border-radius: 0.75rem;
    padding: 1rem 1.5rem;
    color: #eeeeee;
    color: var(--text);
    box-sizing: border-box;
    flex: 1 1;
  }
  
  input:focus-visible {
    outline: 2px solid #52C1FF;
    outline: 2px solid var(--primary);
  }
  
  ::-webkit-input-placeholder {
    font-weight: 700;
  }
  
  ::placeholder {
    font-weight: 700;
  }



@media only screen and (min-width: 1201px) and (max-width: 1400px) {
    .workspace {
      width: 85%;
    }
}
  
@media only screen and (max-width: 1200px) {
.workspace {
    width: 90%;
}
}
.navbar {
    width: 100%;
    height: var(--nav-height);
    background: transparent;
    z-index: 20;
    position: fixed;
    top: 0;
}

.navbar_main {
    height: 100%;
    display: flex;
    align-items: center;
    grid-gap: 20px;
    gap: 20px;
}

.navbar_main_bg {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
}

.go_back_icon {
    width: 30px;
    height: 30px;
    display: flex;
    cursor: pointer;
}

.go_back_icon img {
    margin: auto;
    width: 70%;
    height: 70%;
    object-fit: contain;
    -webkit-transform: rotateZ(180deg);
            transform: rotateZ(180deg);
}
.scanner_section {
    margin: 0px !important;
}

.viewport {
    height: 100vh;
}

.viewport video, .viewport canvas {
    height: 100%;
    width: 100%;
    object-fit: cover;
    position: absolute;
}

.barcode_scanner {
    position: relative;
}

.barcode_overlay_section_bg {
    position: absolute;
    z-index: 10;
    width: 100%;
    bottom: 0;
    padding: 1rem 0px;
    background: var(--background);
}

.barcode_overlay_section {
    display: flex;
    align-items: center;
    grid-gap: min(20px, 2vw);
    gap: min(20px, 2vw);
}

.barcode_overlay_section>input {
    height: 50px;
    min-width: 100px;
}

.barcode_overlay_section>button {
    padding: 0px;
    width: 50px;
    height: 50px;
    border-radius: 0.75rem;
}

.loading_bg {
    width: 100vw;
    height: 100vh;
    position: absolute;
    background: radial-gradient(transparent, black);
    z-index: 11;
}

.loading_bg>img {
    width: 50px;
    height: 50px;
    position: absolute;
    z-index: 11;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}
.home {
    max-width: 500px;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
}

.home_logo, .home_image {
    width: 90%;
}

.home_logo svg, .home_image svg {
    width: 100%;
    height: auto;
}

.home_text {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.home_title{
    font-size: 1.75rem;
    font-weight: 500;
}

.home_subtitle{
    font-size: 1rem;
}
